@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.container {
  max-width: 1440px;
  width: 87.5%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 94.79%;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 94.44%;
  }
}
.page {
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  position: relative;
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
}

.header__logo {
  margin-left: 20px;
}

.header__user-menu {
  margin-left: auto;
}

@media (min-width: 768px) {
  .header {
    grid-template-columns: repeat(3, auto);
  }

  .header__logo {
    margin-right: 38px;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    width: auto;
    margin-right: 77px;
  }
}
.logo {
  grid-column: 1/2;
  grid-row: 1/2;
}

.logo__text {
  display: none;
}

.logo__image--footer {
  width: 82px;
  height: 36px;
}

@media (min-width: 768px) {
  .logo {
    grid-column: 1/2;
    grid-row: 1/2;
  }

  .logo__image {
    width: 82px;
    height: 36px;
  }
  .logo__image--footer {
    width: 170px;
    height: 36px;
  }
}
@media (min-width: 1440px) {
  .logo {
    display: flex;
    align-items: center;
  }

  .logo__image {
    width: 170px;
    height: 36px;
    margin-right: 20px;
  }
  .logo__image--footer {
    margin-right: 0;
    margin-bottom: 8px;
  }

  .logo__text {
    display: block;
    width: 130px;
    margin: 0;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 12px;
    font-weight: 400;
    font-style: normal;
    line-height: 16px;
    color: #333333;
  }
  .logo__text--footer {
    width: 318px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    line-height: 20px;
    color: #333333;
  }
}
.user-menu {
  display: flex;
  flex-wrap: wrap;
  grid-column: 2/3;
  grid-row: 1/2;
}

.user-menu__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  padding: 0;
  text-decoration: none;
}
.user-menu__link:active {
  background-color: #f0ebff;
}

.user-menu__link-text {
  display: none;
}

.user-menu__burger {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  padding: 0;
  border: none;
  background-color: transparent;
}
.user-menu__burger > .user-menu__burger-line, .user-menu__burger > .user-menu__burger-line::before, .user-menu__burger > .user-menu__burger-line::after {
  display: block;
  position: absolute;
  width: 16px;
  height: 1.5px;
  background-color: #333333;
}
.user-menu__burger > .user-menu__burger-line::before {
  content: "";
  top: -5px;
}
.user-menu__burger > .user-menu__burger-line::after {
  content: "";
  top: 5px;
}
.user-menu__burger--close > .user-menu__burger-line {
  background-color: transparent;
}
.user-menu__burger--close > .user-menu__burger-line::before {
  top: 0;
  transform: rotate(45deg);
}
.user-menu__burger--close > .user-menu__burger-line::after {
  top: 0;
  transform: rotate(-45deg);
}
.user-menu__burger--nojs {
  display: none;
}
.user-menu__burger:active {
  background-color: #f0ebff;
}

@media (min-width: 768px) {
  .user-menu {
    grid-column: 3/4;
    grid-row: 1/2;
  }

  .user-menu__link {
    height: 80px;
  }
}
@media (min-width: 1440px) {
  .user-menu__item:first-child {
    margin-right: 20px;
    width: 112px;
  }

  .user-menu__link {
    width: 112px;
  }
  .user-menu__link--basket {
    width: 129px;
  }
  .user-menu__link:focus-visible {
    outline: 1px solid #7859cf;
    outline-offset: 2px;
  }
  .user-menu__link:hover svg {
    fill: #7859cf;
  }
  .user-menu__link:hover > .user-menu__link-text {
    color: #7859cf;
  }
  .user-menu__link:active {
    background-color: #f0ebff;
  }
  .user-menu__link:active > .user-menu__link-text {
    color: #7859cf;
  }

  .user-menu__link-text {
    display: block;
    margin-left: 8px;
    text-transform: uppercase;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 12px;
    font-weight: 400;
    font-style: normal;
    line-height: 16px;
    color: #333333;
  }
}
.navigation {
  grid-column: 1/3;
  grid-row: 2/3;
  width: 100%;
}
.navigation--js {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}

.navigation__item {
  border-bottom: 1px solid #f0ebff;
}
.navigation__item:first-child {
  border-top: 1px solid #f0ebff;
}

.navigation__link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  width: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  line-height: 16px;
  color: #333333;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #ffffff;
}
.navigation__link:active {
  background-color: #f0ebff;
}

@media (min-width: 768px) {
  .navigation {
    grid-column: 2/3;
    grid-row: 1/2;
  }

  .navigation__list {
    display: flex;
    flex-wrap: wrap;
  }

  .navigation__item {
    border: none;
  }
  .navigation__item:first-child {
    border: none;
  }

  .navigation__link {
    padding: 32px 20px;
  }
  .navigation__link--current {
    position: relative;
    color: #7859cf;
  }
  .navigation__link--current::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: #7859cf;
  }
  .navigation__link:focus-visible {
    outline: 1px solid #7859cf;
    outline-offset: 2px;
  }
  .navigation__link:hover {
    color: #7859cf;
  }
  .navigation__link:active {
    color: #7859cf;
    background-color: #f0ebff;
  }
}
@media (min-width: 1440px) {
  .navigation__item:not(:last-child) {
    margin-right: 20px;
  }
}
.main {
  flex-grow: 1;
}

@media (min-width: 768px) {
  .page__header {
    width: 94.79%;
    margin: 0 auto;
  }

  .page__footer {
    width: 94.79%;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .page__header {
    width: 94.44%;
    max-width: 1440px;
  }

  .page__footer {
    width: 94.44%;
    max-width: 1440px;
  }
}
.button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  color: #ffffff;
  text-decoration: none;
  background-color: #7859cf;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  text-align: center;
}
.button:focus-visible {
  outline: 1px solid #7859cf;
  outline-offset: 2px;
}
.button:hover {
  background-color: #9070ec;
}
.button:active {
  background-color: #593cac;
}
.button:disabled {
  background-color: #bdbdbd;
}

.new-text {
  display: block;
  text-align: center;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 700;
  font-style: normal;
  line-height: 16px;
  color: #7859cf;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.swiper {
  position: relative;
}

.swiper__wrapper.swiper-wrapper {
  display: flex;
}

.swiper__item {
  height: auto;
  padding-top: 20px;
  padding-bottom: 60px;
  background-color: #f3ebe1;
}
.swiper__item--latte {
  background-color: #eae6fc;
}
.swiper__item--espresso {
  background-color: #e5e6e8;
}

.swiper__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.swiper__img {
  display: block;
  margin-bottom: 10px;
}

.swiper__new {
  margin-bottom: 4px;
}

.swiper__title {
  margin: 0;
  margin-bottom: 12px;
  text-align: center;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 700;
  font-style: normal;
  line-height: 48px;
  color: #333333;
  letter-spacing: 0.02em;
}

.swiper__description {
  margin: 0;
  margin-bottom: 20px;
  text-align: center;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 20px;
  color: #333333;
}

.swiper__order {
  display: flex;
  align-items: center;
  justify-content: center;
}

.swiper__price {
  margin: 0;
  margin-right: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 600;
  font-style: normal;
  line-height: 32px;
  color: #333333;
}

.swiper__price-del {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 32px;
  color: #7859cf;
}

.swiper__order-link {
  padding: 8px 20px;
}

.swiper__arrow {
  border: none;
  background-color: transparent;
  color: #7859cf;
  height: 100%;
  width: 20px;
  top: 0;
  margin-top: 0;
}
.swiper__arrow::after {
  font-size: 20px;
}
.swiper__arrow:focus-visible {
  outline: 1px solid #7859cf;
  outline-offset: 2px;
}

.swiper-button-prev {
  left: 0;
}
.swiper-button-prev:hover {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.swiper-button-prev:active {
  background-image: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}

.swiper-button-next {
  right: 0;
}
.swiper-button-next:hover {
  background: linear-gradient(-90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.swiper-button-next:active {
  background-image: linear-gradient(-90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}

.swiper__dots {
  display: none;
}

.swiper-pagination-bullet {
  width: 62px;
  height: 4px;
  border-radius: 0%;
  background-color: #ffffff;
  opacity: 1;
}
.swiper-pagination-bullet:hover {
  background-color: #f0ebff;
}
.swiper-pagination-bullet:focus-visible {
  outline: 1px solid #7859cf;
  outline-offset: 2px;
}

.swiper-pagination-bullet-active {
  background-color: #7859cf;
}

@media (min-width: 768px) {
  .swiper__item {
    padding-top: 60px;
    padding-bottom: 0;
    background-image: linear-gradient(to top, #ffffff 22.7%, transparent 22.7%);
  }

  .swiper__container {
    flex-direction: column-reverse;
    width: 79%;
  }

  .swiper__img {
    width: 476px;
    height: 476px;
    margin: 0 auto;
  }

  .swiper__title {
    margin-bottom: 16px;
    font-size: 56px;
    line-height: 80px;
  }

  .swiper__description {
    font-size: 20px;
    line-height: 32px;
  }

  .swiper__order {
    margin-bottom: 20px;
  }

  .swiper__arrow {
    top: 0;
    bottom: 0;
    width: 80px;
  }

  .swiper-button-prev::after {
    margin-bottom: 190px;
  }

  .swiper-button-next::after {
    margin-bottom: 190px;
  }
}
@media (min-width: 1440px) {
  .swiper__item {
    padding-top: 30px;
    padding-bottom: 83px;
    background-image: none;
  }

  .swiper__container {
    flex-direction: row;
    align-items: center;
    width: 83%;
    margin-left: 105px;
    margin-right: 80px;
  }

  .swiper__picture {
    margin-right: 30px;
    flex-shrink: 0;
  }

  .swiper__img {
    width: 600px;
    height: 600px;
  }

  .swiper__content {
    padding-top: 12px;
  }

  .swiper__new {
    text-align: left;
  }

  .swiper__title {
    text-align: left;
  }

  .swiper__description {
    text-align: left;
    margin-bottom: 40px;
    width: auto;
  }

  .swiper__order {
    justify-content: start;
  }

  .swiper__price {
    margin-right: 22px;
  }

  .swiper__dots.swiper-pagination {
    display: block;
    bottom: 66px;
  }

  .swiper-button-prev::after {
    margin-bottom: 0;
  }

  .swiper-button-next::after {
    margin-bottom: 0;
  }
}
.title-text {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 700;
  font-style: normal;
  line-height: 36px;
  color: #333333;
  text-align: center;
}

@media (min-width: 768px) {
  .title-text {
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 42px;
    font-weight: 700;
    font-style: normal;
    line-height: 64px;
    color: #333333;
  }
}
.features {
  padding: 50px 0 60px;
}

.features__new {
  margin-bottom: 4px;
}

.features__title {
  margin-bottom: 36px;
}

.features__list {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  row-gap: 32px;
}

.features__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-top: 66px;
  position: relative;
}
.features__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  width: 50px;
  height: 56px;
  background-repeat: no-repeat;
  background-position: center;
}
.features__item--speed::before {
  background-image: url("../img/icon/features-speed.svg");
}
.features__item--convenience::before {
  background-image: url("../img/icon/features-conveniece.svg");
}
.features__item--energy::before {
  background-image: url("../img/icon/features-energy.svg");
}
.features__item--ecology::before {
  background-image: url("../img/icon/features-ecology.svg");
}

.features__item-title {
  margin: 0;
  margin-bottom: 4px;
  text-align: center;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  font-style: normal;
  line-height: 32px;
  color: #333333;
}

.features__item-description {
  margin: 0;
  text-align: center;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 20px;
  color: #333333;
}

@media (min-width: 768px) {
  .features {
    padding: 60px 0 100px;
  }

  .features__container {
    width: 92.1%;
  }

  .features__title {
    margin-bottom: 62px;
  }

  .features__list {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 50px;
    column-gap: 30px;
    justify-items: start;
  }

  .features__item {
    align-items: start;
    padding-top: 0;
    padding-left: 71px;
  }
  .features__item::before {
    left: 0;
    transform: translate(0);
  }

  .features__item-title {
    margin-bottom: 10px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 700;
    font-style: normal;
    line-height: 36px;
    color: #333333;
  }

  .features__item-description {
    text-align: left;
    font-family: "Montserrat", "Arial", sans-serif;
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    line-height: 24px;
    color: #333333;
  }
}
@media (min-width: 1440px) {
  .features {
    padding: 100px 0 120px;
  }

  .features__title {
    margin-bottom: 82px;
    text-align: center;
  }

  .features__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 30px;
  }

  .features__item {
    align-items: center;
    padding-left: 0;
    padding-top: 76px;
  }
  .features__item::before {
    left: 50%;
    transform: translate(-50%);
  }

  .features__item-description {
    text-align: center;
  }
}
.catalog {
