  font-size: 20px;
  font-weight: 600;
  font-style: normal;
  line-height: 32px;
  color: #333333;
}

.catalog__item-button {
  position: relative;
  padding: 8px 20px 8px 46px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
