  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #9070ec;
}
.filter__range-toggle--min {
  top: -9px;
  left: 0;
}
.filter__range-toggle--max {
  top: -9px;
  right: 20px;
}
