  outline: 1px solid #7859cf;
  outline-offset: 2px;
}
.filter__range-toggle:hover {
  background-color: #f0ebff;
  box-shadow: inset 0 0 0 2px #ffffff;
}
.filter__range-toggle:active {
  background-color: #7859cf;
  box-shadow: inset 0 0 0 2px #ffffff;
}
.filter__range-toggle--disabled {
  background-color: #bdbdbd;
}

.filter__inputs {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.filter__inputs::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 49%;
  width: 9px;
  height: 2px;
  background-color: #bdbdbd;
}

.filter__mark {
  display: block;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  border: 2px solid #bdbdbd;
}

.filter__input[type=radio] + .filter__mark {
  border-radius: 50%;
}
.filter__input[type=radio] + .filter__mark:hover {
  border-color: #7859cf;
  background-color: #f0ebff;
  box-shadow: inset 0 0 0 2px #ffffff;
}
.filter__input[type=radio]:checked + .filter__mark {
  border-color: #7859cf;
  background-color: #7859cf;
  box-shadow: inset 0 0 0 2px #ffffff;
}
.filter__input[type=radio]:disabled + .filter__mark + .filter__value {
  opacity: 0.5;
}
.filter__input[type=checkbox] + .filter__mark {
  position: relative;
  width: 42px;
  border-radius: 12px;
  background-color: #bdbdbd;
}
.filter__input[type=checkbox] + .filter__mark:hover {
  background-color: #f0ebff;
}
.filter__input[type=checkbox] + .filter__mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transition-duration: 0.5s;
  transform: translate(0%, -50%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #ffffff;
}
.filter__input[type=checkbox]:checked + .filter__mark {
  border-color: #7859cf;
  background-color: #7859cf;
}
.filter__input[type=checkbox]:checked + .filter__mark::before {
  transition-duration: 0.5s;
  transform: translate(90%, -50%);
}
.filter__input[type=checkbox]:disabled + .filter__mark + .filter__value {
  opacity: 0.5;
}
.filter__input:focus-within + .filter__mark {
  outline: 2px solid #7859cf;
  outline-offset: 2px;
}
