.map__image {
  display: block;
  margin: 0 auto;
  width: 100%;
  object-fit: contain;
}

.map__picture {
  &--hidden {
    display: none;
  }
}

.map__canvas {
  width: 100%;
  height: 420px;
}

@media (min-width: $tablet) {
  .map__canvas {
    height: 480px;
  }

  .map__image {
    width: 768px;
  }
}

@media (min-width: $desktop) {
  .map__canvas {
    height: 540px;
  }

  .map__image {
    width: 1440px;
  }
}
