.catalog {
  background-color: $color-lightest-grey;
  padding-top: 50px;
  padding-bottom: 45px;
}

.catalog__new {
  margin-bottom: 4px;
}

.catalog__title {
  margin-bottom: 20px;
}

.catalog__filter {
  margin-bottom: 39px;
}

.catalog__select-wrapper {
  margin: 0 0 20px auto;
}

.catalog__select-label {
  display: flex;
  align-items: center;

  @include fontMontserrat(600, 16px, 24px, $color-base-black);
}

.catalog__content {
  margin-bottom: 40px;
}

.catalog__list-wrapper {
  display: grid;
}

.catalog__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.catalog__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px 20px 20px;
  background-color: $color-base-white;
  border-radius: 4px;

  &:hover {
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  }
}

.catalog__link-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  width: 100%;

  &:focus-visible {
    outline: 1px solid $color-base-primary;
    outline-offset: 2px;
  }
}

.catalog__image {
  margin-bottom: 20px;

  &--first {
    margin-bottom: 15px;
  }
}

.catalog__item-title {
  margin: 0;
  margin-bottom: 4px;
  text-align: left;
  width: 100%;

  @include fontMontserrat(700, 18px, 32px, $color-base-black);
}

.catalog__item-text {
  margin: 0;
  margin-bottom: 16px;
  align-self: start;

  @include fontMontserrat(400, 14px, 20px, $color-base-black);
}

.catalog__item-order {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.catalog__item-price {
  @include fontMontserrat(600, 20px, 32px, $color-base-black);
}

.catalog__item-button {
  position: relative;
  padding: 8px 20px 8px 46px;

  @include fontMontserrat(400, 14px, 20px, $color-base-white);

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    width: 20px;
    height: 16px;
    background-image: url("../img/icon/catalog-basket.svg");
  }
}

@media (min-width: $tablet) {
  .catalog {
    padding-top: 80px;
    padding-bottom: 88px;
  }

  .catalog__filter {
    margin-bottom: 40px;
  }

  .catalog__content {
    margin-bottom: 70px;
  }

  .catalog__select-label {
    margin: 0 0 4px auto;
  }

  .catalog__select-name {
    margin-right: 5px;
  }

  .catalog__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 28px;
  }

  .catalog__item {
    padding: 50px 30px 30px 30px;
  }

  .catalog__image {
    margin-bottom: 28px;
  }

  .catalog__item-title {
    margin-bottom: 10px;

    @include fontMontserrat(700, 24px, 36px, $color-base-black);
  }

  .catalog__item-text {
    margin-bottom: 20px;
  }
}

@media (min-width: $desktop) {
  .catalog {
    background-color: $color-lightest-grey;
    padding-top: 100px;
    padding-bottom: 120px;
  }

  .catalog__title {
    margin-bottom: 40px;
  }

  .catalog__content {
    display: grid;
    grid-template-columns: 318px 1fr;
    gap: 30px;
    margin-bottom: 60px;
  }

  .catalog__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }

  .catalog__image {
    margin-bottom: 24px;
  }
}
